#footer {
  background: url('../images/footer-bg.png') no-repeat center top / cover;
  padding-top: 100px;
  padding-bottom: 80px;
}

.footer-title {
  font-weight: 400;
  font-size: 40px;
  line-height: 124%;
  letter-spacing: 0.03em;
  color: #fff;
  margin-bottom: 100px;
}

.footer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 105px;
}

.footer-item {
  letter-spacing: 0.03em;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 14px;
  line-height: 140%;
  color: #fff;
}

@media (max-width: 810px) {
  #footer {
    background: url('../images/footer-bg.png') no-repeat right 31% top / cover;
  }
  
}

@media (max-width: 576px) {
  #footer {
    padding-top: 40px;
    padding-bottom: 10px;
  }

  .footer-title {
    font-size: 22px;
    line-height: 150%;
    margin-bottom: 30px;
  }
}
