#utility {
  padding-top: 150px;

  .container {
    padding-right: 0;
  }
}

.after-title__block {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 60px;
  max-width: 1320px;
}

.slider-arrow {
  display: flex;
  align-items: center;
}

.slider__arrow {
  cursor: pointer;
}

.slider__arrow:last-child {
  margin-left: 30px;
}

.utility-slide {
  background: #f4f5f7;
  border-radius: 16px;
  padding-top: 40px;
  padding-left: 40px;
  height: auto;
}

.utility-slider__img {
  height: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
}

.slider-arrow__mobile {
  display: none;
}

@media (max-width: 1150px) {
  #utility {
    padding-top: 60px;

    .container {
      padding-right: 0;
    }
  }

  #manager {
    padding-top: 70px;
  }

  #employee {
    padding-top: 70px;
  }

  #convenience {
    padding: 70px 0;
  }

  .utility-slider__img {
    margin-top: -20px;
  }
}

@media (max-width: 700px) {
  .slider-arrow__desk {
    display: none;
  }

  .slider-arrow__mobile {
    display: flex;
    margin-top: 24px;
    justify-content: center;
  }

  .after-title__block {
    margin-bottom: 20px;
  }
}

@media (max-width: 576px) {
  .after-title__block {
    align-items: flex-end;
  }

  .slider-title {
    font-size: 14px;
  }

  .utility-slider__img {
    margin-top: -121px;
  }

  .utility-slide {
    height: 300px;
  }
}

