#manager {
  padding-top: 200px;
}

.manager-role {
  padding-top: 60px;
  display: flex;
  justify-content: space-between;
}

.manager-role__item:first-child {
  flex-basis: 45%;
}

.manager-role__item:last-child {
  flex-basis: 50%;
}

.manager-role__content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.manager-role__item-block {
  width: 310px;
  background: #fff;
  box-shadow: 0px 8px 34px rgba(98, 118, 169, 0.1);
  border-radius: 10px;
  padding: 20px;
  height: min-content;
  margin-bottom: 20px;
}

.role-head {
  display: flex;
}

.role-title {
  font-weight: 500;
  font-size: 13px;
  line-height: 140%;
  letter-spacing: 0.03em;
  padding: 8px;
  background: #f1f8ff;
  width: initial;
  margin-right: 13px;
  border-radius: 10px;
  color: #1199f0;
}

.role-text {
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  letter-spacing: 0.03em;
  color: #060606;
  margin-top: 14px;
}

.utility-btn {
  padding: 20px 25px;
  margin-top: 40px;
  display: inline;
}

.button-try {
  margin-top: 40px;
}

.manager-role__img_mob {
  display: none;
}

@media (max-width: 1400px) {
  .manager-role__item:first-child {
    flex-basis: 35%;
  }

  .manager-role__item:last-child {
    flex-basis: 55%;
  }
}

@media (max-width: 1200px) {
  .manager-role {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .manager-role__item:first-child {
    margin-bottom: 40px;
  }
}

@media (max-width: 576px) {
  .manager-role__content {
    justify-content: center;
  }

  .manager-role__item .button-try {
    text-align: center;
  }

  .manager-role__img_desk {
    display: none;
  }

  .manager-role__img_mob {
    display: flex;
  }

  .manager-role__item-block {
    width: 100%;
  }

  .utility-btn {
    display: block;
  }
}