#convenience {
  padding: 200px 0;
}

.convenience-head {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}

.convenience {
  display: flex;
  justify-content: space-between;
  margin-top: 70px;
}

.convenience-item {
  flex-basis: 50%;
}

.convenience-adv__item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 66px;
}

.convenience-adv__icon {
  height: 60px;
  width: 60px;
  border-radius: 50%;
  background: #f4f5f7;
  display: flex;
  align-items: center;
  justify-content: center;
}

.convenience-adv__content {
  margin-left: 32px;
}

.convenience-adv__title {
  font-weight: 400;
  font-size: 20px;
  line-height: 150%;
  letter-spacing: 0.03em;
  color: #0e105a;
}

.convenience-adv__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  letter-spacing: 0.03em;
  color: #060606;
  margin-top: 10px;
  max-width: 400px;
}

.convenience-slider {
  width: 680px;
  padding-bottom: 15px;
}

.swiper-pagination-bullet {
  background: var(--swiper-pagination-color,var(--swiper-theme-color));
}

.convenience-adv__item:last-child {
  margin-bottom: 0;
}

@media (max-width: 1350px) {
  .convenience {
    flex-wrap: wrap;
  }

  .convenience-item {
    flex-basis: 100%;
    order: 1;
    margin-bottom: 30px;
  }

  .convenience-item:first-child {
    order: 2;
  }
}

@media (max-width: 700px) {
  .convenience-slider {
    width: 380px;
  }
}

@media (max-width: 400px) {
  .convenience-slider {
    width: 320px;
  }
}

@media (max-width: 576px) {
  .convenience-adv__text {
    max-width: 300px;
  }

  .convenience {
    margin-top: 0;
  }

  .convenience-adv__title {
    font-size: 16px;
    line-height: 150%;
  }

  .convenience-adv__text {
    font-size: 14px;
    line-height: 140%;
  }
}
