#main-block {
  background: url('../images/main-bg.png') no-repeat center top / cover;
  height: 100vh;
}

.main-block {
  padding-top: 200px;
  display: flex;
  justify-content: space-between;
}

.main-title {
  font-weight: 400;
  font-size: 40px;
  line-height: 124%;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  color: #fff;
  margin-bottom: 54px;
}

.main-subtitle {
  font-weight: 400;
  font-size: 20px;
  line-height: 150%;
  letter-spacing: 0.02em;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  color: #fff;
  margin-bottom: 136px;
}

.button-block {
  width: 367px;
}

.button-block__app {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.btn-app {
  background: #fff;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 19px;
  width: 175px;
}

.main-block__item-img {
  padding-top: 120px;
}

.main-block__item-img_mob {
  display: none;
}

.button-block__btn {
  display: block;
  text-align: center;
}

@media (max-width: 920px) {
  .main-block {
    padding-top: 150px;
    flex-direction: column;
  }

  .main-title {
    margin-bottom: 30px;
  }

  .main-subtitle {
    margin-bottom: 30px;
  }

  .main-block__item-img {
    padding-top: 30px;
  }
}

@media (max-width: 810px) {
  .main-title {
    font-size: 36px;
  }

  .main-block__item-img {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .main-block__item-img img {
    height: 400px;
  }

  #main-block {
    background: url(../images/main-bg.png) no-repeat right 22% top/cover;
  }
}

@media (max-width: 576px) {
  #main-block {
    height: 100vh;
    background: url(../images/main-bg.png) no-repeat right 22% top/cover;
  }

  .main-title {
    font-size: 22px;
    margin-bottom: 34px;
  }

  .main-subtitle {
    margin-bottom: 40px;
    font-size: 14px;
  }

  .main-block__item-img {
    padding-top: 40px;
  }

  .main-block__item-img_desk {
    display: none;
  }

  .main-block__item-img_mob {
    display: block;
  }

  .main-block__item-img img {
    height: auto;
  }

  .button-block {
    width: 100%;
  }

  .button-block__app {
    margin-top: 8px;
  }
}

@media (max-width: 370px) {
  .btn-app {
    width: 145px;
    padding: 10px;
  }
}

@media (max-width: 1600px) and (max-height: 910px) {
  #main-block {
    height: 110vh;
  }
}

@media (max-width: 1370px) and (max-height: 768px) {
  #main-block {
    height: 115vh;
  }

  .main-subtitle {
    margin-bottom: 40px;
  }

  .main-block__item-img {
    padding-top: 60px;
  }
}

@media (max-width: 920px) and (min-width: 576px) {
  #main-block {
    height: 133vh;
  }
}

@media (max-width: 415px) and (max-height: 740px) {
  #main-block {
    height: 110vh;
  }
}

@media (max-width: 320px) and (max-height: 570px) {
  #main-block {
    height: 132vh;
  }
}

@media (max-height: 720px) {
  #main-block {
    height: 122vh;
  }
}

@media (max-height: 600px) {
  .main-subtitle {
    margin-bottom: 30px;
  }
}

@media (max-width: 375px) and (max-height: 668px) {
  #main-block {
    height: 135vh;
  }
}

@media (max-height: 420px) {
  #main-block {
    height: 300vh;
  }
}

