html {
  height: 100%; // Footer at bottom
  font-size: 62.5%;
}

body {
  display: flex; // Footer at bottom
  flex-direction: column; // Footer at bottom
  height: 100%; // Footer at bottom
  color: #000;
  font-size: 1.4rem;
  line-height: 1.6rem;
  background: #fff;
  font-family: 'PT Root UI';
  font-style: normal;
}

.header {
  flex-shrink: 0; // Footer at bottom
}

.main {
  position: relative;
  flex: 1 0 auto; // Footer at bottom
}

.footer {
  z-index: 10;
  flex-shrink: 0; // Footer at bottom
  margin-top: auto;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: top;
  border: none;

  table & {
    max-width: none;
  }
}

a {
  text-decoration: none;
}

.container {
  padding: 0 15%;
  margin: 0 auto;
}

.title {
  font-weight: 400;
  font-size: 40px;
  line-height: 124%;
  color: #0e105a;
  margin-bottom: 30px;
}

.after-title__text {
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.02em;
  color: #060606;
  max-width: 540px;
}

.slider-title {
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  letter-spacing: 0.02em;
  color: #0e105a;
  width: 360px;
  height: 30px;
}

.btn {
  font-weight: 700;
  font-size: 17px;
  line-height: 140%;
  letter-spacing: 0.01em;
  color: #fff;
  padding: 20px;
  border: none;
  background: #1199f0;
  border-radius: 8px;
  box-shadow: 0px 10px 40px rgba(98, 118, 169, 0.2);
}

.btn:hover {
  background: #198AD1;
  box-shadow: 0px 10px 40px rgba(98, 118, 169, 0.2);
}

.btn:active {
  background: #1199F0;
  box-shadow: 0px 10px 40px rgba(98, 118, 169, 0.2);
}

@media (max-width: 1800px) {
  .container {
    padding: 0 10%;
    margin: 0 auto;
  }
}

@media (max-width: 1550px) {
  .container {
    padding: 0 7%;
    margin: 0 auto;
  }
}

@media (max-width: 1550px) {
  .container {
    padding: 0 5%;
    margin: 0 auto;
  }
}

@media (max-width: 1250px) {
  .container {
    padding: 0 3%;
    margin: 0 auto;
  }
}

@media (max-width: 1150px) {
  .slider-title {
    width: auto;
    padding-right: 20px;
    height: 60px;
  }
}

@media (max-width: 1050px) {
  .btn {
    padding: 20px 45px;
  }
}

@media (max-width: 700px) {
  .title {
    font-size: 22px;
  }

  .after-title__text {
    font-size: 14px;
    line-height: 140%;
  }
}

@media (max-width: 576px) {
  .slider-title {
    height: 120px;
    font-size: 13px ;
  }

  .btn {
    font-size: 15px;
  }
}

@media (max-width: 360px) {
  .btn {
    padding: 20px;
  }
}