#header {
  position: absolute;
  background: transparent;
  width: 100%;
  z-index: 1;
  padding-top: 40px;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.header-btn {
  font-weight: 700;
  font-size: 15px;
  line-height: 140%;
  letter-spacing: 0.03em;
  background: transparent;
  padding: 14px 16px;
  border: 1px solid #fff;
}

.header-btn:hover {
  background: #198AD1;
  border: 1px solid #198AD1;
}

.header-btn:active {
  background: #1199F0;
  border: 1px solid #1199F0;
}

@media (max-width: 430px) {
  .logo {
    width: 122px;
  }

  .header-btn {
    padding: 13px 14px;
    font-size: 13px;
  }
}

@media (max-width: 360px) {
  .header-btn {
    padding: 13px 7px;
  }
}