#employee {
  padding-top: 150px;

  .container {
    padding-right: 0;
  }
}

.employee-slide {
  background: #f4f5f7;
  border-radius: 16px;
  padding-top: 40px;
  padding-left: 40px;
  height: 500px;
}

.employee-slider__img {
  text-align: center;
  margin-top: 20px;
}

.employee .after-title__block .after-title__text {
  max-width: 475px;
}

@media (max-width: 1375px) {
  .employee-slide {
    height: 400px;
  }
}

@media (max-width: 991px) {
  .employee-slide {
    height: 450px;
  }
}

@media (max-width: 700px) {
  .employee-slide {
    height: 350px;
  }

  .employee-slider__img {
    margin-top: 0;
  }
}

@media (max-width: 576px) {
  .employee-slide .slider-title {
    height: 40px;
  }

  .employee-slide {
    height: 490px;
  }
}

@media (max-width: 375px) {
  .employee-slide {
    height: 450px;
  }
}

@media (max-width: 350px) {
  .employee-slide {
    height: 400px;
  }
}